		<select v-model="vm.currentImageCategoryId" v-on:change="vm.changeCurrentImageCategory()">
			<option v-for="category in vm.imageCategories"
			v-bind:key="category.id" :value="category.id">{{category.name}}</option>
		</select>
		-->

		<span v-for="category in vm.imageCategories" v-bind:key="category.id">
			<label>
				<input style="margin-left: 10px;" type="radio" v-model="vm.currentImageCategoryId" :value="category.id" v-on:change="vm.changeCurrentImageCategory()">{{category.name}}
